<template>
    <div id="appdiyadai" style="text-align: left;">
        <ContractTop tle="报备审批" tleurl="/index" style="position:fixed;top:0px;z-index:999;background:#fff;"></ContractTop>
        <mt-navbar v-model="selected" style="width:100%;position:fixed;top:44px;z-index:999;background:#fff;" value=1 :swipeable='true'>
            <div style="width:450px;">
                <mt-tab-item id="1">委托协议</mt-tab-item>
                <mt-tab-item id="2">{{ type?'押车报备单':'报备单' }}</mt-tab-item>
                <mt-tab-item id="3">流程附件</mt-tab-item>
                <mt-tab-item id="4">审批意见</mt-tab-item>
            </div>
        </mt-navbar>

        <mt-tab-container v-model="selected" :swipeable='true' style="margin-top:88px;">
            <mt-tab-container-item id="1">
                <weituoxieyi v-bind:xieyi='xieyi'></weituoxieyi>
            </mt-tab-container-item>
            <mt-tab-container-item id="2">
                <chebaobei v-if="xieyi.carPropertyModel" v-bind:showsave="showsave" v-bind:buchong="buchong" v-bind:chebaobei="chebaobei" @child-info="saveche"></chebaobei>
                <fangbaobei v-else v-bind:fangbaobei="fangbaobei" v-bind:buchong="buchong" v-bind:showsave="showsave" @child-info="savefang"></fangbaobei>
            </mt-tab-container-item>
            <mt-tab-container-item id="3">
                <liuchengfujian v-bind:liuchengall="liuchengcontent" v-bind:contracttype="contracttype"></liuchengfujian>
            </mt-tab-container-item>
            <mt-tab-container-item id="4">
                <shenpiyijian v-bind:history="history" v-bind:showhistory="showhistory" v-on:approve="approve" v-on:unapprove="unapprove"></shenpiyijian>
            </mt-tab-container-item>
        </mt-tab-container>
    </div>
</template>

<script>
    import { TabContainer, TabContainerItem } from 'mint-ui';
    import axios from 'axios';
    import { Tabbar, TabItem } from 'mint-ui';
    import ContractTop from '@/components/ContractTop';
    import WeiTuoXieYi from '@/components/ZhiyaWeituoxieyi';
    import CheBaoBei from '@/components/ZhiyaChebaobei';
    import FangBaoBei from '@/components/ZhiyaFangbaobei';
    import liuchengfujian from '@/components/liuchengfujian';
    import shenpiyijian from '@/components/shenpiyijian';
    import qs from 'qs';
    import { Toast } from 'mint-ui';

    export default {
        data () {
            return {
                selected:'1',
                contractId:'',
                xieyi:'',
                chebaobei:'',
                fangbaobei:'',
                liuchengcontent:'',
                contracttype:'3',
                contractnum:'',
                type:'',
                baocun:'',
                tijiao:'',
                history:'',
                showhistory:true,
                showsave:true,
                id:'',
                buchong:'',
                step:'',
                typeid:'',
                userId:'819384343152005129',
                flag:false,
            }
        },
        created:function(){
//            console.log(this.$route.params);
            this.contractnum = this.$route.params.contractnum;
            this.contractid = this.$route.params.contractid;
            this.processinstanceid=this.$route.params.processinstanceid;
            this.step=this.$route.params.step;
            this.typeid=this.$route.params.typeid;
            //委托协议的回显
            axios.post('http://47.93.41.211:12308/app/loadTrustContractMain?contractnum='+this.contractnum,{
                headers:{'Content-Type': 'application/x-www-form-urlencoded'},
            }).then(response=>{
                console.log('zhiya');
                console.log(response.data.data.zhiya);
                this.xieyi=response.data.data.zhiya;
                this.contractId=response.data.data.zhiya.contractId;
                this.type=response.data.data.zhiya.carPropertyModel;
            //报备单回显
            axios.post('http://47.93.41.211:12308/app/loadReport?contractid='+this.contractId,{
                headers:{'Content-Type': 'application/x-www-form-urlencoded'},
            }).then(response=>{
                console.log('baobei');
                 console.log(response.data.data);
                //这里需要添加判断是车报备还是房报备
                axios.post('http://tsapi.hcxzdanbao.com/contract/baobeichehuixian',qs.stringify({
                    contractId:this.contractId,
                })).then(response=>{
                    console.log('huixian');
                    console.log(response.data.data);
                    this.buchong = response.data.data;

                }).catch(function(err){
                    Toast('操作失败');
                });
                if(this.type){
                    this.chebaobei=response.data.data;
                }else{
                    this.fangbaobei=response.data.data;
                }

        }).catch(function(err){
                Toast('操作失败');
            });
                //审批历史
                axios.post('http://tsapi.hcxzdanbao.com/contract/optionhistory',qs.stringify({
                    contractId:this.contractId,
                    isbaobei:true,
                })).then(response=>{
                    if(response.data.code=='200'){
                        this.history = response.data.data;
                    }
                }).catch(function(err){
                    Toast('操作失败');
                });
        //图片回显
        axios.get("http://47.93.41.211:12308/getaccessory?contractid="+this.contractId,{
            headers:{'Content-Type': 'application/x-www-form-urlencoded'},
        }).then(response=>{
            this.liuchengcontent=response.data;
//            console.log(this.liuchengcontent);
        }).catch(function(err){
            Toast('操作失败');
        })

        }).catch(function(error){
                console.log(error);
        });
        },
        components:{
            'ContractTop':ContractTop,
            'weituoxieyi':WeiTuoXieYi,
            'chebaobei':CheBaoBei,
            'fangbaobei':FangBaoBei,
            'liuchengfujian':liuchengfujian,
            'shenpiyijian':shenpiyijian,
        },
        methods:{
            approve:function(data){
                if(this.flag==false){
                    Toast("请保存报备单");
                    return false;
                }
                axios.post('http://tsapi.hcxzdanbao.com/contract/approve',qs.stringify({
                    processId:this.processinstanceid,
                    step:this.step,
                    contractId:this.contractId,
                    message:data,
                    userId:this.userId,
                    type:1,
                })).then(response=>{
                   console.log(response);
                   if(response.data.code=="200"){
//                       this.$router.push({name: 'contractbaobei', params: {}});
                   }
                }).catch(function(err){
                    Toast('操作失败');
                });
            },
            unapprove:function(data){
                if(this.flag==false){
                    Toast("请保存报备单");
                    return false;
                }
                axios.post('http://tsapi.hcxzdanbao.com/contract/disagree',qs.stringify({
                    processId:this.processinstanceid,
                    step:this.step,
                    contractId:this.contractId,
                    message:data,
                    userId:this.userId,
                    type:1,
                })).then(response=>{
                    console.log(response);
                    if(response.data.code=="200"){
//                       this.$router.push({name: 'contractbaobei', params: {}});
                    }
                }).catch(function(err){
                    Toast('操作失败');
                });
            },
            //报备单保存
            savefang(data){
                console.log(data);
                if(data.fangwuguzhis=="" ||data.diyalv=="" || data.rongzishangxian==""){
                    Toast("请提交完整数据");
                    return false;
                }
                axios.post('http://tsapi.hcxzdanbao.com/contract/baobeidan',qs.stringify(
        data
                )).then(response=>{
                    this.flag=true;
                    console.log(response);
                }).catch(function(err){
                    Toast('操作失败');
                })
            },
            //押车报备单保存
            saveche(data){
                console.log(data);
                if(data.approvevalue=="" ||data.financelimit=="" ||data.mortgagerate==""){
                    Toast("请提交完整数据");
                    return false;
                }
                axios.post('http://tsapi.hcxzdanbao.com/contract/baobeidanche',qs.stringify(
                    data
//                    approvevalue:approvevalue,
//                    financelimit:financelimit,
//                    mortgagerate:mortgagerate,
//                    lettername:lettername,
//                    approveopinion:approveopinion,
//                    contractId:this.contractId,
//                    id:this.id,
            )).then(response=>{
                    this.flag=true;
                    console.log(response);
                }).catch(function(err){
                    Toast('操作失败');
                })
            },
            //意见提交
            commit(result){
                this.tijiao = result;
                alert(result);
            }
        }
    }
</script>

<style scoped>
    @import "../assets/css/contract.css";
    *{color:#333;}
    input{color:#666;}
    input[disabled],input:disabled{
        /*border:1px solid #;*/
        background-color:#fff;
        color:#666;
        opacity:1 !important;
        -webkit-opacity:1 !important;
    }
    textarea[disabled],textarea:disabled{
        background-color:#fff;
        color:#666;
        opacity:1 !important;
        -webkit-opacity:1 !important;
    }
    .ret:before{
        height: 16px;
        width: 9px;
        display: inline-block;
        float: left;
        vertical-align: middle;
        position: absolute;
        margin: auto 0;
        left:2%;
        top:9px;
    }
    /*流程css 模块：附件上传 彭*/
    .imgupBox{
        margin-bottom: 18px;
        overflow: hidden;
    }
    .groupPhotoC,.IDPhotoC,.itPhotoC{
        padding: 0px 15px;
    }
    .groupPhoto,.IDPhoto,.itPhoto{
        border-top:9px solid #f5f5f5;
        overflow: hidden;
    }
    .Phototit{
        font-size: 14px;
        color: #666;
        line-height: 35px;
    }
    /*上传按钮*/
    .imgupbtn{
        border: 1px dashed #dadada;
        width: 29%;
        height:82px;
        position:relative;
        float: left;
    }
    .imgupbtnInp{
        opacity:0;
        filter:alpha(opacity=0);
        height: 82px;
        width: 100px;
        position: absolute;
        /*position:relative;*/
        top: 0;
        /*top: -64px;*/
        left: 0;
        z-index: 9;
    }
    .rep{
        padding: 0px 0px;
        margin: 0px 0px;
        overflow: hidden;
    }
    .rep>div{
        width: 29%;
        height: 84px;
        float: left;
        margin-right: 2.09%;
        margin-left: 2.09%;
        margin-top: 12px;
    }
    .imgCC{
        position:relative;
    }
    .imgCC>img{
        height: 100%;
        width: 100%;
    }
    .ImgClose{
        width: 20px;
        height:20px;
        background-color: #fff;
        background: url(@import "../assets/img/cancel.png") no-repeat;
        background-size: 100% 100%;
        position:absolute;
        right: -8%;
        top: -8%;
    }
    /*流程css结束*/
    .hide{display: none;}
</style>


