<template>
    <div class="returnContract">
        <ContractTop tle="已办报备审批" tleurl="/index" style="position: fixed;top:0px;z-index: 10;"></ContractTop>
        <mt-loadmore style="position:absolute;top:48px;"
                     :top-method="handleTopChange"
                     :bottom-method="handleBottomChange"
                     :bottom-all-loaded="allLoaded"
                     :auto-fill="false"
                     ref="loadmore"
        >
        <div v-for="contactData in items">
            <ul class="contract">
                <li class="state2">
                    <p class="state_1"><span>申请人：</span><span class="state_1m2">{{ contactData.submitterName }}</span></p>
                </li>
            <li class="Guardian3">
                <p class="Guardian_1">
                    <span class="Guardian_1w" style="margin-right: 0px">审批详情：</span><span class="Guardian_1p" style="margin-left: 0px">{{ contactData.processName }}</span></p>
                </li>
            <li class="uptime4">
                <p class="Guardian_2">
                    <span class="Guardian_2w">审核意见：</span><span class="Guardian_2zhuang">{{ contactData.auditorOpinion }}</span>
                    </p>
                </li>
            <li class="uptime4">
                <p class="Guardian_2">
                    <span class="Guardian_2w">审核时间：</span><span class="Guardian_2zhuang">{{ dealtime(contactData.updateTime) }}</span>
                    </p>
                </li>
            <li class="uptime4">
                <p class="uptime_1">
                    <span class="uptime_1t">审批状态：</span>
                    <span class="uptime_1tc">{{ contactData.approval==1 ? "已同意":"已驳回" }}</span>
                </p>
            </li>
            </ul>
        </div>
        <div v-show="show" style="margin-top:44px;">
            <noData></noData>
        </div>
        </mt-loadmore>
    </div>
</template>

<script>
    import ContractTop from '@/components/ContractTop';
    import { TabContainer, TabContainerItem } from 'mint-ui';
    import { Tabbar, TabItem } from 'mint-ui';
    import axios from 'axios';
    import noData from '@/components/noData';
    import qs from 'qs';
    import {Loadmore} from 'mint-ui';
    import { Toast } from 'mint-ui';

    export default {
        data () {
            return {
                userId:'819384343152005129',
                items:[],
                page:1,
                pageSize:15,
                topStatus: '',
                allLoaded: false, //是否可以上拉属性，false可以上拉，true为禁止上拉，就是不让往上划加载数据了
                scrollMode:"auto", //移动端弹性滚动效果，touch为弹性滚动，auto是非弹性滚动
                show:false,
            }
        },
        mounted(){
            this.load();
        },
        components:{
            'ContractTop':ContractTop,
            'noData':noData,
            'v-loadmore':Loadmore,
        },
        methods:{
            load(){
                axios.post('http://tsapi.hcxzdanbao.com/contractyb/baobeilist',qs.stringify({
                    page:this.page,
                    pageSize:this.pageSize,
                    userId:this.userId,
                })).then(response=>{
                    if(response.data.code=='200'){
                        this.show=false;
                        if(response.data.data=='' && this.page==1){
                            this.show=true;
                            this.allLoaded = true;
                            this.items=[];
                        }else{
                            this.items = this.items.concat(response.data.data);
                            console.log(response.data.data);
                            if(response.data.data==''){
                                this.allLoaded = true;
                            }
//                            console.log(this.items);
                        }
                    }
                }).catch(function(err){
                    Toast('操作失败');
                });
            },
            handleTopChange() {
                // 加载更多数据
                setTimeout(() => {
                    this.page=1;
                    this.items=[];
                    this.load();
                    this.$refs.loadmore.onTopLoaded();
                }, 1500);

            },
            handleBottomChange() {
                // 加载更多数据
//                this.allLoaded = true;// 若数据已全部获取完毕
//                this.$refs.loadmore.onBottomLoaded();
                setTimeout(() => {
                    this.page +=1;
                    this.load();
                    this.$refs.loadmore.onBottomLoaded();
                }, 1500);
            },
            dealtime(times){
                var date = new Date(times);
                return date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate();
            },
        }
    }

</script>

