<<template>
    <form id="buchong" action="">
        <div class="iterm">
            <label for="">机构名：</label>
            <input type="text" class="word5" name="organname" id="organname" v-model="buchong.organname" />
        </div>
        <div class="iterm">
            <label for="">客户名：</label>
            <input type="text" class="word5" name="custname" id="custname" v-model="buchong.custname"/>
        </div>
        <div class="iterm">
            <label for="">区域：</label>
            <input type="text" class="word5" name="region" id="region" v-model="buchong.region"/>
        </div>
        <div class="iterm">
            <label for="">部门：</label>
            <input type="text" class="word5" name="deptname"  id="deptname" v-model="buchong.deptname"/>
        </div>
        <div class="iterm">
            <label for="">报备人姓名：</label>
            <input type="text" class="word5" name="reportname" v-model="buchong.reportname" />
        </div>
        <div class="iterm">
            <label for="">报备时间：</label>
            <input type="date" class="word5" name="reporttime" id="reporttime" v-model="buchong.reporttime"/>
        </div>
        <div class="iterm">
            <label for="">经理姓名：</label>
            <input type="text" class="word5" name="managername" id="managername" v-model="buchong.managername"/>
        </div>
        <div class="iterm">
            <label for="">主管姓名：</label>
            <input type="text" class="word5" name="directorname" id="directorname" v-model="buchong.directorname"/>
        </div>
        <div class="iterm">
            <label for="">业务姓名：</label>
            <input type="text" class="word5" name="salesmanname" id="salesmanname" v-model="buchong.salesmanname"/>
        </div>
        <div class="iterm">
            <label for="">借款期限：</label>
            <input type="text" class="word5" name="loantime" id="loantime" v-model="buchong.loantime" />
        </div>
        <div class="iterm">
            <label for="">借款金额（万元）：</label>
            <input type="text" class="word5" name="loanmoney" id="loanmoney" v-model="buchong.loanmoney" />
        </div>
        <div class="iterm">
            <label for="">点位及返点：</label>
            <input type="text" class="word6" name="point" id="point" style="display:inline-block;" v-model="buchong.point"/>
        </div>
        <div class="iterm">
            <label for="">服务费金额：</label>
            <input type="text" class="word5" name="servicemoney"  id="servicemoney" v-model="buchong.servicemoney"/>
        </div>
        <div class="iterm">
            <label for="">返费金额（元）：</label>
            <input type="text" class="word5" name="returnmoney" id="returnmoney" v-model="buchong.returnmoney"/>
        </div>
        <div class="iterm">
            <label for="">车辆评估值：</label>
            <input type="text" class="word5" name="carvalue" v-model="buchong.carvalue"/>
        </div>
        <div class="iterm">
            <label for="">尾款（万元）：</label>
            <input type="text" class="word5" name="retainagemoney" v-model="buchong.retainagemoney"/>
        </div>
        <div class="iterm">
            <label for="">排量：</label>
            <input type="text" class="word5" name="displacement" v-model="buchong.displacement"/>
        </div>
        <div class="iterm">
            <label for="">行驶证日期：</label>
            <input type="date" class="word5" name="drivetime" v-model="buchong.drivetime"/>
        </div>

        <div class="iterm">
            <span class="star">*</span>
            <label for="">车辆款型：</label>
            <input type="text" class="word5" name="vehiclemodel" id="vehiclemodel" v-model="buchong.vehiclemodel"/>
        </div>
        <div class="iterm">
        <label for="">审批员意见</label>
        </div>
        <div class="iterm">
        <label for="">车辆评估值：</label>
        <input type="text" class="word5" v-model="buchong.approvevalue" name="approvevalue"/>
        </div>
        <div class="iterm">
        <label for="">融资上限：</label>
        <input type="text" class="word5" v-model="buchong.financelimit" name="financelimit"/>
        </div>
        <div class="iterm">
        <label for="">抵押率：</label>
        <input type="text" class="word5" v-model="buchong.mortgagerate" name="mortgagerate"/>
        </div>
        <div class="iterm">
        <label for="">信审员签字：</label>
        <input v-bind:disabled="showor" type="text" name="lettername" v-model="lettername"/>
        </div>
        <div class="iterm">
        <label for="">审批人意见：</label>
        <input type="text" class="word5" name="approveopinion" v-model="buchong.approveopinion"/>
        </div>
        <div class="big-save" @click="send" id="formCheBao-submit" v-show="showsave">
            保存
        </div>
    </form >
</template>

<script>
    export default {
        data () {
          return {
              lettername:'短融部',
              showor:true,
          }
        },
        props:['buchong','showsave'],
        methods:{
            send(){
                this.$emit('child-info',this.buchong);
            }
        }
    }
</script>


