<template>
  <div id="app">
      <ContractTop tle="银行客户" tleurl="/index" style="position:fixed;top:0px;z-index: 999;background-color:#fff;"></ContractTop>
      <ContractHead v-on:open="trans" v-on:contidion="getContidion" style="width:100%;position:fixed;top:54px;z-index: 999;background-color:#fff;"></ContractHead>
      <mt-loadmore
              :top-method="handleTopChange"
              :bottom-method="handleBottomChange"
              :bottom-all-loaded="allLoaded"
              :auto-fill="false"
              ref="loadmore"
              :class="cls"
      >
	  <li v-for="item in items"><ContractBlock  v-bind:message="item"></ContractBlock></li>
          <noData v-show="show" :class="cls"></noData>
      </mt-loadmore>
      <!--<mt-button type="danger" style="position:fixed;right:30px;bottom:50px;color:#fff;" @click="goTop">回到顶部</mt-button>-->
	</div>
</template>
<script>
import ContractTop from '@/components/ContractTop';
import ContractHead from '@/components/ContractHead';
import ContractBlock from '@/components/ContractBlock';
import { InfiniteScroll } from 'mint-ui';
import { Indicator } from 'mint-ui';
import axios from 'axios';
import { Button } from 'mint-ui';
import qs from 'qs';
import noData from '@/components/noData';
import { Toast } from 'mint-ui';

export default {
  data () {
  return {
      open:false,
	  popupVisible4: false,
      show:false,
      cls:'class1',
	  loading: false,

	  items:[],

      userId:'819384343152005127',
      page:1,
      pageSize:15,
      typeId:'',
      custName:'',
      state:'',
      department:'',
      accountNum:'',

      allLoaded: false, //是否可以上拉属性，false可以上拉，true为禁止上拉，就是不让往上划加载数据了
      scrollMode:"auto", //移动端弹性滚动效果，touch为弹性滚动，auto是非弹性滚动
    }
  },
  mounted(){
      this.load();
  },
  methods:{
      getContidion(state,custName,department,type,accountNum){
          this.state=state;
          this.custName=custName;
          this.department=department;
          this.typeId=type;
          this.accountNum=accountNum;
          this.items=[];
          this.load();
      },
      trans(open){
//          console.log("open:"+open);
          if(open){
              this.cls="class2";
          }else{
              this.cls="class1";
          }
      },
	  load() {
          this.loading = true;
          Indicator.open({
              text: '加载中...',
              spinnerType: 'fading-circle'
          });
          axios.post('http://tsapi.hcxzdanbao.com/yhmq/userlist ', qs.stringify({
              page:this.page,
              pageSize:this.pageSize,
              userId:this.userId,
              department:this.department,
              state:this.state,
              custName:this.custName,
              typeId:this.typeId,
              accountNum:this.accountNum,
		  })).then(response=>{
              if(response.data.code=='200'){
                  this.show=false;
                  if(response.data.data=='' && this.page==1){
//                      this.show=true;
                      this.allLoaded=true;
                      this.items=[];
                      this.show=true;
                  }else{
                      if(response.data.data==''){
                          this.allLoaded=true;
                      }
                      this.items = this.items.concat(response.data.data);
                            console.log(response.data.data);
//                            console.log(this.items);
                  }
              }
              Indicator.close();
		  }).catch(function (error) {
              Toast('操作失败');
		  });
      },
      handleTopChange() {
          // 加载更多数据
          setTimeout(() => {
              this.page=1;
              this.items=[];
              this.load();
              this.$refs.loadmore.onTopLoaded();
          }, 1500);

      },
      handleBottomChange() {
          // 加载更多数据
          setTimeout(() => {
              this.page +=1;
              this.load();
              this.$refs.loadmore.onBottomLoaded();
          }, 1500);
      },
      goTop(){
          document.body.scrollTop = document.documentElement.scrollTop = 0;
      },
  },
  components:{
      'ContractTop':ContractTop,
      'ContractBlock':ContractBlock,
      'ContractHead':ContractHead,
      'noData':noData,
  }
}
</script>

<style scoped>
	@import "../assets/css/server.css";
	.mint-popup-bottom {
        width: 100%;
    }
    li{
        list-style-type: none;
    }
    .class1{
        margin-top:100px;
    }
    .class2{
        margin-top:440px;
    }
  #app{background:#efeff4;}
</style>



