<template>
    <div class="message">
        <ContractTop tle="消息" tleurl="/index" style="position:fixed;top:0px;z-index:999;background:#fff;" ></ContractTop>
        <mt-navbar v-model="selected" style="width:100%;position:fixed;top:44px;z-index:999;background:#fff;" value=1>
            <div>
                <mt-tab-item id="1" style="width:50%;">未读</mt-tab-item>
                <mt-tab-item id="2" style="width:50%;">已读</mt-tab-item>
            </div>
        </mt-navbar>

        <mt-tab-container v-model="selected" style="margin-top: 88px;" :swipeable='true'>
            <mt-tab-container-item id="1">
                <unread v-bind:pmsglist="pmsglist" v-bind:pmsgshow="pmsgshow" v-on:send="reload"></unread>
            </mt-tab-container-item>
            <mt-tab-container-item id="2">
                <read v-bind:readlist="readlist" v-bind:readshow="readshow"></read>
            </mt-tab-container-item>
        </mt-tab-container>
    </div>
</template>

<script>

    import { TabContainer, TabContainerItem } from 'mint-ui';
    import ContractTop from '@/components/ContractTop';
    import axios from 'axios';
    import {Toast} from 'mint-ui';
    import read from '@/components/old/messageYES';
    import unread from '@/components/old/messageNO';

    export default {
        data(){
            return {
                selected:'1',
                userId:'819384343152005129',
                usertype:'',
                uuid:'123456',
                account:'liliang',
                userName:'liliang',
                pmsglist:'',
                readlist:'',
                pmsgshow:false,
                readshow:false,
            }
        },
        methods:{
            reload(message){
                if(message=='200'){
                    //清除消息缓存，重新获取。以实现页面重新加载的效果
                    this.pmsgshow=false;
                    this.readshow=false;
                    this.pmsglist="";
                    this.readlist="";
                    this.getMessage();
                }
            },
            getMessage(){
                axios.get(host+"/app/msglist?userid="+this.userId,{

                }).then(response=>{
                    console.log(response);
                    //未读消息
                    this.pmsglist=response.data.data.pmsglist;
                    //控制未读消息为空时的状态
                    if(this.pmsglist==''){
                        this.pmsgshow=true;
                    }
                    //已读消息
                    this.readlist=response.data.data.readlist;
                    //控制已读消息为空时的状态
                    if(this.readlist==""){
                        this.readshow=true;
                    }
                }).catch(function(err){
                    Toast("操作失败");
                })
            }
        },
        created(){
            this.getMessage();
        },
        components:{
            'read':read,
            'unread':unread,
            'ContractTop':ContractTop,
        }
    }
</script>


